import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Layout } from "../../layouts";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import {
  IconButton,
  InputAdornment,
  TextField,
  SelectChangeEvent,
  Select,
  MenuItem,
} from "@mui/material";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import AddIcon from "@mui/icons-material/Add";

const ProfileSetup = () => {
  const navigate = useNavigate();

  const [showPassword1, setShowPassword1] = useState(false);
  const handleClickShowPassword1 = () => {
    setShowPassword1(!showPassword1);
  };
  const handleMouseDownPassword1 = (event: any) => {
    event.preventDefault();
  };

  const [showPassword2, setShowPassword2] = useState(false);
  const handleClickShowPassword2 = () => {
    setShowPassword2(!showPassword2);
  };
  const handleMouseDownPassword2 = (event: any) => {
    event.preventDefault();
  };

  const [selectField, setSelectField] = React.useState("default");
  const handleChange = (event: SelectChangeEvent) => {
    setSelectField(event.target.value as string);
  };

  return (
    <Layout>
      <main className="content auth_page">
        <section className="auth_sc uh_spc">
          <div className="conta_iner">
            <div className="inner">
              <div className="hd_4 text_center">
                <a onClick={() => navigate("/login")} className="back_icon">
                  <ArrowBackIcon />
                </a>
                <h2>Profile Setup</h2>
              </div>
              <form className="form" method="post">
                <div className="control_group text_center">
                  <div className="upload_image">
                    <figure>
                      <img src="/static/images/user_icon_placeholder.svg" />
                    </figure>
                    <div className="action">
                      <AddIcon />
                      <input type="file" />
                    </div>
                  </div>
                </div>
                <div className="control_group">
                  <label>Full name</label>
                  <TextField
                    hiddenLabel
                    placeholder="Enter here"
                    fullWidth
                    name="fullName"
                  />
                </div>
                <div className="control_group">
                  <label>Email Id</label>
                  <TextField
                    hiddenLabel
                    placeholder="Enter here"
                    fullWidth
                    name="email"
                  />
                </div>
                <div className="control_group">
                  <label>Phone number</label>
                  <TextField
                    hiddenLabel
                    placeholder="Enter here"
                    fullWidth
                    name="phone"
                  />
                </div>
                <div className="control_group">
                  <label>Gender</label>
                  <Select
                    fullWidth
                    labelId="demo-multiple-name-label"
                    id="demo-multiple-name"
                    label="social-account"
                    value={selectField}
                    onChange={handleChange}
                    name="gender"
                  >
                    <MenuItem disabled value="default">
                      Select
                    </MenuItem>
                    <MenuItem value={10}>Male</MenuItem>
                    <MenuItem value={20}>Female</MenuItem>
                    <MenuItem value={30}>Non-Binary</MenuItem>
                  </Select>
                </div>
                <div className="control_group">
                  <label>Date of birth</label>
                  <TextField
                    hiddenLabel
                    placeholder="Enter here"
                    fullWidth
                    name="dob"
                  />
                </div>
                <div className="control_group">
                  <label>Height</label>
                  <TextField
                    hiddenLabel
                    placeholder="Enter here"
                    fullWidth
                    name="height"
                  />
                </div>
                <div className="control_group">
                  <label>Weight</label>
                  <TextField
                    hiddenLabel
                    placeholder="Enter here"
                    fullWidth
                    name="weight"
                  />
                </div>
                <div className="control_group">
                  <label>Martial status</label>
                  <TextField
                    hiddenLabel
                    placeholder="Enter here"
                    fullWidth
                    name="martialStatus"
                  />
                </div>
                <div className="control_group">
                  <label>Country, City</label>
                  <TextField
                    hiddenLabel
                    placeholder="Enter here"
                    fullWidth
                    name="address"
                  />
                </div>
                <div className="control_group">
                  <label>Occupation</label>
                  <TextField
                    hiddenLabel
                    placeholder="Enter here"
                    fullWidth
                    name="occupation"
                  />
                </div>
                <div className="control_group">
                  <label>Password</label>
                  <TextField
                    hiddenLabel
                    placeholder="Enter Password"
                    fullWidth
                    name="password"
                    type={showPassword1 ? "text" : "password"}
                    variant="outlined"
                    InputProps={{
                      endAdornment: (
                        <InputAdornment className="eye_btn" position="end">
                          <IconButton
                            aria-label="toggle password visibility"
                            onClick={handleClickShowPassword1}
                            onMouseDown={handleMouseDownPassword1}
                            edge="end"
                          >
                            {showPassword1 ? <Visibility /> : <VisibilityOff />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                  />
                </div>
                <div className="control_group">
                  <label>Confirm Password</label>
                  <TextField
                    hiddenLabel
                    placeholder="Enter Password"
                    fullWidth
                    name="confrmPassword"
                    type={showPassword2 ? "text" : "password"}
                    variant="outlined"
                    InputProps={{
                      endAdornment: (
                        <InputAdornment className="eye_btn" position="end">
                          <IconButton
                            aria-label="toggle password visibility"
                            onClick={handleClickShowPassword2}
                            onMouseDown={handleMouseDownPassword2}
                            edge="end"
                          >
                            {showPassword2 ? <Visibility /> : <VisibilityOff />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                  />
                </div>
                <div className="form_btn">
                  <button
                    type="submit"
                    className="btnn btn_xsm btn_primary w_100"
                    onClick={() => navigate("/step-1")}
                  >
                    Next
                  </button>
                </div>
              </form>
            </div>
          </div>
        </section>
      </main>
    </Layout>
  );
};

export default ProfileSetup;
