import React, { useState } from "react";
import { IconButton, InputAdornment, TextField } from "@mui/material";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";

export default function ChangePassword() {

    const [showPassword1, setShowPassword1] = useState(false);
    const handleClickShowPassword1 = () => {
        setShowPassword1(!showPassword1);
    };
    const handleMouseDownPassword1 = (event: any) => {
        event.preventDefault();
    };

    const [showPassword2, setShowPassword2] = useState(false);
    const handleClickShowPassword2 = () => {
        setShowPassword2(!showPassword2);
    };
    const handleMouseDownPassword2 = (event: any) => {
        event.preventDefault();
    };

    const [showPassword3, setShowPassword3] = useState(false);
    const handleClickShowPassword3 = () => {
        setShowPassword3(!showPassword3);
    };
    const handleMouseDownPassword3 = (event: any) => {
        event.preventDefault();
    };

    return (
        <div className="tab-pane fade" id="v-pills-Password" role="tabpanel" aria-labelledby="v-pills-Password-tab">
            <div className="account_head">
                <h3>Change Password</h3>
            </div>
            <form className="form">
                <div className="control_group">
                    <label>Old Password</label>
                    <TextField
                        hiddenLabel
                        placeholder="Enter Password"
                        fullWidth
                        name="password"
                        type={showPassword1 ? "text" : "password"}
                        variant="outlined"
                        InputProps={{
                            endAdornment: (
                                <InputAdornment className="eye_btn" position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword1}
                                        onMouseDown={handleMouseDownPassword1}
                                        edge="end"
                                    >
                                        {showPassword1 ? <Visibility /> : <VisibilityOff />}
                                    </IconButton>
                                </InputAdornment>
                            ),
                        }}
                    />
                </div>
                <div className="control_group">
                    <label>New Password</label>
                    <TextField
                        hiddenLabel
                        placeholder="Enter Password"
                        fullWidth
                        name="password"
                        type={showPassword1 ? "text" : "password"}
                        variant="outlined"
                        InputProps={{
                            endAdornment: (
                                <InputAdornment className="eye_btn" position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword1}
                                        onMouseDown={handleMouseDownPassword1}
                                        edge="end"
                                    >
                                        {showPassword1 ? <Visibility /> : <VisibilityOff />}
                                    </IconButton>
                                </InputAdornment>
                            ),
                        }}
                    />
                </div>
                <div className="control_group">
                    <label>Confirm Password</label>
                    <TextField
                        hiddenLabel
                        placeholder="Enter Password"
                        fullWidth
                        name="password"
                        type={showPassword3 ? "text" : "password"}
                        variant="outlined"
                        InputProps={{
                            endAdornment: (
                                <InputAdornment className="eye_btn" position="end">
                                    <IconButton
                                        aria-label="toggle password visibility"
                                        onClick={handleClickShowPassword3}
                                        onMouseDown={handleMouseDownPassword3}
                                        edge="end"
                                    >
                                        {showPassword3 ? <Visibility /> : <VisibilityOff />}
                                    </IconButton>
                                </InputAdornment>
                            ),
                        }}
                    />
                </div>
                <div className="form_btn">
                    <button type="submit" className="btnn btn_xsm btn_primary">Submit</button>
                </div>
            </form>
        </div>
    )
}
