import React, { Dispatch, SetStateAction } from 'react';
import { Modal, Button, TextField, SelectChangeEvent, Select, MenuItem } from '@mui/material';
import { generateResponsiveStyle } from '../utils/authModalStyle';
import CloseIcon from "@mui/icons-material/Close";
import AddIcon from '@mui/icons-material/Add';

interface editProfileModalProps {
    open: boolean;
    onClose: () => void;
    setOpen: Dispatch<SetStateAction<boolean>>
}

export default function EditProfileModal({ open, onClose, setOpen }: editProfileModalProps) {

    const style = generateResponsiveStyle()

    const [selectField, setSelectField] = React.useState('default');
    const handleChange = (event: SelectChangeEvent) => {
        setSelectField(event.target.value as string);
    };

    return (
        <Modal
            className="modal form_modal"
            id="editProfileModal"
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
            open={open}
            onClose={onClose}
        >
            <div className="modal-dialog">
                <div className="modal-body hd_4">
                    <div className="btn-close" onClick={() => setOpen(false)}>
                        <CloseIcon />
                    </div>
                    <h2>Edit Profile</h2>
                    <form className="form">
                        <div className="gap_p">
                            <div className="control_group text_center">
                                <div className="upload_image">
                                    <figure><img src="static/images/placeholder.jpg" alt="icon" /></figure>
                                    <div className="action">
                                        <AddIcon />
                                        <input placeholder="img" type="file" />
                                    </div>
                                </div>
                            </div>
                            <div className="control_group w_50">
                                <label>Full name</label>
                                <TextField hiddenLabel placeholder="Enter here" fullWidth />
                            </div>
                            <div className="control_group w_50">
                                <label>Email Id</label>
                                <TextField hiddenLabel placeholder="Enter here" fullWidth type="email" />
                            </div>
                            <div className="control_group w_50">
                                <label>Phone number</label>
                                <TextField hiddenLabel placeholder="Enter here" fullWidth />
                            </div>
                            <div className="control_group w_50">
                                <label>Gender</label>
                                <Select
                                    fullWidth
                                    labelId="demo-multiple-name-label"
                                    id="demo-multiple-name"
                                    label="social-account"
                                    value={selectField}
                                    onChange={handleChange}
                                >
                                    <MenuItem disabled value="default">Select</MenuItem>
                                    <MenuItem value={10}>Male</MenuItem>
                                    <MenuItem value={20}>Female</MenuItem>
                                    <MenuItem value={30}>Non-Binary</MenuItem>
                                </Select>
                            </div>
                            <div className="control_group w_50">
                                <label>Date of birth</label>
                                <TextField hiddenLabel placeholder="Enter here" fullWidth type="date" />
                            </div>
                            <div className="control_group w_50">
                                <label>Height</label>
                                <TextField hiddenLabel placeholder="Enter here" fullWidth />
                            </div>
                            <div className="control_group w_50">
                                <label>Weight</label>
                                <TextField hiddenLabel placeholder="Enter here" fullWidth />
                            </div>
                            <div className="control_group w_50">
                                <label>Martial status</label>
                                <TextField hiddenLabel placeholder="Enter here" fullWidth />
                            </div>
                            <div className="control_group w_50">
                                <label>Country, City</label>
                                <TextField hiddenLabel placeholder="Enter here" fullWidth />
                            </div>
                            <div className="control_group w_50">
                                <label>Occupation</label>
                                <TextField hiddenLabel placeholder="Enter here" fullWidth />
                            </div>
                        </div>
                        <div className="btn_flex">
                            <a className="c_danger" href="javascript:void(0)" onClick={() => setOpen(false)}>Cancel</a>
                            <Button className="btnn btn_xsm btn_primary">Save</Button>
                        </div>
                    </form>
                </div>
            </div>
        </Modal>
    )
}
