import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Checkbox, IconButton, InputAdornment, TextField } from "@mui/material";
import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";import CheckIcon from '@mui/icons-material/Check';

const SignIn = () => {
  const navigate = useNavigate();
  const [isEmail, setIsEmail] = useState(false);
  // console.log(isEmail);

  const [showPassword1, setShowPassword1] = useState(false);
  const handleClickShowPassword1 = () => {
    setShowPassword1(!showPassword1);
  };
  const handleMouseDownPassword1 = (event: any) => {
    event.preventDefault();
  };

  const [showPassword2, setShowPassword2] = useState(false);
  const handleClickShowPassword2 = () => {
    setShowPassword2(!showPassword2);
  };
  const handleMouseDownPassword2 = (event: any) => {
    event.preventDefault();
  };

  return (
    <>
      {isEmail ? (
        <div className="tab-pane">
          <div className="control_group">
            <label>Phone Number</label>
            {/* <input type="text"  /> */}
            <TextField hiddenLabel placeholder="Enter here" fullWidth name="phonenumber" />
          </div>
          <div className="control_group">
            <label>Password</label>
            <TextField
              hiddenLabel
              placeholder="Enter Password"
              fullWidth
              name="password"
              type={showPassword1 ? "text" : "password"}
              variant="outlined"
              InputProps={{
                endAdornment: (
                  <InputAdornment className="eye_btn" position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword1}
                      onMouseDown={handleMouseDownPassword1}
                      edge="end"
                    >
                      {showPassword1 ? <Visibility /> : <VisibilityOff />}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />
          </div>
          <div className="control_group control_group_flex">
            <label className="checkbox_label">
              <Checkbox /> Remember me
            </label>
            <p>
              <small>
                <a href="javascript:void(0)" onClick={() => navigate("/forgot-password")}>
                  Forgot Password
                </a>
              </small>
            </p>
          </div>
          <div className="form_btn">
            <button type="submit" className="btnn btn_xsm btn_primary w_100">
              Login
            </button>
          </div>
        </div>
      ) : (
        <div className="tab-pane">
          <div className="control_group">
            <label>Email</label>
            <TextField hiddenLabel placeholder="Enter here" fullWidth name="phonenumber" />
          </div>
          <div className="control_group">
            <label>Password</label>
            <TextField
              hiddenLabel
              placeholder="Enter Password"
              fullWidth
              name="password"
              type={showPassword2 ? "text" : "password"}
              variant="outlined"
              InputProps={{
                endAdornment: (
                  <InputAdornment className="eye_btn" position="end">
                    <IconButton
                      aria-label="toggle password visibility"
                      onClick={handleClickShowPassword2}
                      onMouseDown={handleMouseDownPassword2}
                      edge="end"
                    >
                      {showPassword2 ? <Visibility /> : <VisibilityOff />}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />
          </div>
          <div className="control_group control_group_flex">
            <label className="checkbox_label">
              <Checkbox /> Remember me
            </label>
            <p>
              <small>
                <a href="javascript:void(0)" onClick={() => navigate("/forgot-password")}>
                  Forgot Password
                </a>
              </small>
            </p>
          </div>
          <div className="form_btn">
            <button
              type="submit"
              className="btnn btn_xsm btn_primary w_100"
              onClick={() => navigate("/")}
            >
              Login
            </button>
          </div>
        </div>
      )}

      <div className="text_tabs">
        {!isEmail ? (
          <button
            className="nav-link"
            type="button"
            onClick={() => setIsEmail(true)}
          >
            Sign up with Phone Number
          </button>
        ) : (
          <button
            className="nav-link"
            type="button"
            onClick={() => setIsEmail(false)}
          >
            Sign up with Email
          </button>
        )}
      </div>
    </>
  );
};

export default SignIn;
